*{
  margin:0;
  font-weight: lighter;
  font-family: Candara;
}
h1{
  font-size: 2.5rem;
}
h2 {
  width: 100%; 
  text-align: center; 
  border-bottom: 3px solid purple; 
  line-height: 0.1;
  margin: 10px 0 20px; 
} 
h2 span { 
   background:#fff; 
   padding:0 10px; 
}
h3{
  font-size: 1.25rem;
}
footer{
  text-align: center;
  padding: 1vh;
}

.flex{
  display:flex;
  flex-wrap: wrap;
}
.wrap1{
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.wrap2{
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.titleBar{
  position: sticky;
  width: 100%;
}
.bars{
  text-decoration: none;
  transition: 0.5s;
  background-color: white;
  color: black;
  font-weight: lighter;
  margin-left: 3vw;
  margin-top: 2vh;
}
.bars:hover{
  color: purple;
}
.itemIMG{
  width: 350px;
  height: 350px;
}
.formz{
  text-align: center;
}
.formp{
  width: 25vw;
  padding: 1vh;
  font-size: 1.25vw;
}

.fav{
  border-radius: 4px;
  background-color: white;
  border: 5px solid purple;
  color: black;
  text-align: center;
  font-size: 1.25vw;
  padding: 1vh;
  transition: all 0.5s;
  cursor: pointer;

}
